body {
  margin: 0;
  padding: 0;
  background-color: black;
  display: flex;
  justify-content: center;
  height: 100vh;
  align-items: center;
  color: rgb(255, 255, 255);
  font-family: Arial, Helvetica, sans-serif;
}

.app {
  position: relative;
  height: 750px;
  width: 380px;
  border-radius: 40px;
  background-color: rgb(38, 38, 38);
  padding: 20px;
}

svg {
  fill: rgb(255, 255, 255);
}

nav {
  display: flex;
  justify-content: space-between;
  padding: 20px 0;
}

h1,
h2,
p {
  margin: 4px;
}

h1 {
  font-size: 24px;
}

.sub-text {
  color: rgb(114, 114, 114);
}

a {
  text-decoration: none;
  color: rgb(114, 114, 114);
}

header .info-container {
  display: flex;
  justify-content: space-between;
}

header .img-container {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  overflow: hidden;
}

header .img-container img {
  height: 110%;
}

header .threads-info {
  background-color: rgb(59, 59, 59);
  color: rgb(114, 114, 114);
  font-size: 13px;
  padding: 3px 6px;
  border-radius: 20px;
}

button.primary {
  border: rgb(114, 114, 114) solid 1px;
  background-color: transparent;
  padding: 7px;
  margin: 15px 0;
  border-radius: 7px;
  color: rgb(255, 255, 255);
  font-weight: bold;
  width: 100%;
}

.button-container button {
  border: none;
  background-color: transparent;
  padding: 7px;
  color: rgb(255, 255, 255);
  font-weight: bold;
  width: 50%;
  border-bottom: rgb(114, 114, 114) solid 1px;
}

.button-container button.current {
  border-bottom: rgb(255, 255, 255) solid 2px;
}

.feed-card .icons svg {
  height: 20px;
  padding: 0 5px;
}

.feed-card {
  border-bottom: rgb(114, 114, 114) solid 1px;
  padding: 10px 0;
}

.feed-card .text-container {
  display: flex;
  justify-content: space-between;
}

.feed-card .img-container {
  height: 30px;
  width: 30px;
  border-radius: 50%;
  overflow: hidden;
}

.feed-card .img-container img {
  height: 100%;
}

.feed {
  height: 400px;
  overflow-y: scroll;
}

.feed::-webkit-scrollbar {
  width: 12px;
}
.feed::-webkit-scrollbar-track {
  background-color: #ccc;
  box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.5);
  border-radius: 10px;
}
.feed::-webkit-scrollbar-thumb {
  background-color: rgb(114, 114, 114);
  border-radius: 10px;
}

input {
  background-color: transparent;
  border: rgb(114, 114, 114) solid 1px;
  padding: 10px;
  width: 100%;
  margin: 10px 0;
  box-sizing: border-box;
  border-radius: 5px;
  color: rgb(255, 255, 255);
}

input:focus {
  outline: none;
}

.popup {
  position: absolute;
  bottom: 0;
  left: 0;
  height: 650px;
  width: 380px;
  border-radius: 40px;
  background-color: rgb(38, 38, 38);
  padding: 20px;
  box-shadow: rgba(0, 0, 0, 0.25) 0 54px 55px, rgba(0, 0, 0, 0.12) 0 -12px 30px,
    rgba(0, 0, 0, 0.12) 0 4px 6px, rgba(0, 0, 0, 0.12) 0 12px 13px, rgba(0, 0, 0, 0.17) 0 -3px 5px;
}

.write-icon {
  fill: rgb(255, 255, 255);
  height: 30px;
  margin-top: 20px;
  margin-left: 170px;
}
